import { Type_RefFuncKrpano } from '@components/Krpano/types';
import { useTourEventState } from '@containers/Home/global/TourStats';
import { useControlbarConfig } from '@containers/Home/hook/useControlbarConfig';
import { useKrpano } from '@containers/Home/hook/useKrpano';
import { LANGUAGES } from '@containers/Home/language';
import {
  selectConfigSoundProject,
  selectLanguageProject,
} from '@containers/Home/selectors';
import { Popover, Tooltip } from 'antd';
import React, { memo } from 'react';
import { useSelector } from 'react-redux';
import { ControlbarPopoverWrapper } from './styled';

interface Props {
  krpanoRef: Type_RefFuncKrpano;
}

const ControlbarAction = memo(({ krpanoRef }: Props) => {
  const [{ isSoundOnScene, isActiveModeMultipleStaging }] =
    useTourEventState('state');
  const { onRenderContentControlbarItem, onCheckIsShowContentControlbarItem } =
    useControlbarConfig();

  const languageProject = useSelector(selectLanguageProject);
  const configSoundProject = useSelector(selectConfigSoundProject);

  const {
    //
    onVR,
    onToggleGyro,
    onFullscreen,
    onToFirstScene,
    onViewNormal,
    onViewMirrorBall,
    onViewPlannet,
    // NOTE: auto change scene
    isChangeScene,
    isShowIconChangeScene,
    onChangeAutoChangeScene,
    // NOTE: auto rotate
    isRotate,
    isShowIconRotate,
    onToggleRotate,
    // NOTE: info place
    isShowIconInfoPlace,
    onShowInfoPlace,
    // NOTE: sound scene
    isShowIconSoundScene,
    onToggleSoundScene,
    // NOTE: sound project
    isShowIconSoundProject,
    onTurnOffSoundProject,
    onTurnOnSoundProject,
    // NOTE: floorplan
    isShowIconFloorplan,
    onToggleFloorplan,
    // NOTE: map
    isShowIconMap,
    onToggleMap,
    // NOTE: multiple staging
    isShowIconMultipleStaging,
    onToggleModeMultipleStaging,
  } = useKrpano({ krpanoRef });

  return (
    <div className="actGroup">
      {onCheckIsShowContentControlbarItem('home') && (
        <Tooltip
          title={LANGUAGES[languageProject].controlbarFirstScene}
          placement="top"
        >
          <button type="button" className="act" onClick={onToFirstScene}>
            {onRenderContentControlbarItem('home')}
          </button>
        </Tooltip>
      )}
      {isShowIconInfoPlace && onCheckIsShowContentControlbarItem('info_tour') && (
        <Tooltip
          title={LANGUAGES[languageProject].controlbarInfoTour}
          placement="top"
        >
          <button type="button" className="act" onClick={onShowInfoPlace}>
            {onRenderContentControlbarItem('info_tour')}
          </button>
        </Tooltip>
      )}
      {isShowIconFloorplan && onCheckIsShowContentControlbarItem('floorplan') && (
        <Tooltip
          title={LANGUAGES[languageProject].controlbarFloorplan}
          placement="top"
        >
          <button type="button" className="act" onClick={onToggleFloorplan}>
            {onRenderContentControlbarItem('floorplan')}
          </button>
        </Tooltip>
      )}
      {isShowIconMap && onCheckIsShowContentControlbarItem('map') && (
        <Tooltip
          title={LANGUAGES[languageProject].controlbarMap}
          placement="top"
        >
          <button type="button" className="act" onClick={onToggleMap}>
            {onRenderContentControlbarItem('map')}
          </button>
        </Tooltip>
      )}
      {isShowIconSoundProject &&
        onCheckIsShowContentControlbarItem('sound_project_off') &&
        onCheckIsShowContentControlbarItem('sound_project_on') && (
          <Tooltip
            title={
              configSoundProject.on
                ? LANGUAGES[languageProject].controlbarTurnOffSoundProject
                : LANGUAGES[languageProject].controlbarTurnOnSoundProject
            }
            placement="top"
          >
            <button
              type="button"
              className="act"
              onClick={
                configSoundProject.on
                  ? onTurnOffSoundProject
                  : onTurnOnSoundProject
              }
            >
              {configSoundProject.on
                ? onRenderContentControlbarItem('sound_project_off')
                : onRenderContentControlbarItem('sound_project_on')}
            </button>
          </Tooltip>
        )}
      {((isShowIconSoundScene &&
        onCheckIsShowContentControlbarItem('sound_scene_off') &&
        onCheckIsShowContentControlbarItem('sound_scene_on')) ||
        (isShowIconRotate &&
          onCheckIsShowContentControlbarItem('auto_rotate_off') &&
          onCheckIsShowContentControlbarItem('auto_rotate_on')) ||
        (isShowIconChangeScene &&
          onCheckIsShowContentControlbarItem('auto_change_scene_off') &&
          onCheckIsShowContentControlbarItem('auto_change_scene_on'))) && (
        <Popover
          placement="top"
          zIndex={99}
          content={
            <ControlbarPopoverWrapper>
              <ul>
                {isShowIconSoundScene &&
                  onCheckIsShowContentControlbarItem('sound_scene_off') &&
                  onCheckIsShowContentControlbarItem('sound_scene_on') && (
                    <li>
                      <button type="button" onClick={onToggleSoundScene}>
                        {!isSoundOnScene
                          ? onRenderContentControlbarItem('sound_scene_on')
                          : onRenderContentControlbarItem('sound_scene_off')}
                        {LANGUAGES[languageProject].controlbarSoundScene}
                      </button>
                    </li>
                  )}
                {isShowIconRotate &&
                  onCheckIsShowContentControlbarItem('auto_rotate_off') &&
                  onCheckIsShowContentControlbarItem('auto_rotate_on') && (
                    <li>
                      <button type="button" onClick={onToggleRotate}>
                        {!isRotate
                          ? onRenderContentControlbarItem('auto_rotate_on')
                          : onRenderContentControlbarItem('auto_rotate_off')}
                        {isRotate
                          ? LANGUAGES[languageProject].controlbarStopRotate
                          : LANGUAGES[languageProject].controlbarStartRotate}
                      </button>
                    </li>
                  )}
                {isShowIconChangeScene &&
                  onCheckIsShowContentControlbarItem('auto_change_scene_off') &&
                  onCheckIsShowContentControlbarItem(
                    'auto_change_scene_on',
                  ) && (
                    <li>
                      <button
                        type="button"
                        onClick={onChangeAutoChangeScene.bind(
                          this,
                          !isChangeScene,
                        )}
                      >
                        {!isChangeScene
                          ? onRenderContentControlbarItem(
                              'auto_change_scene_on',
                            )
                          : onRenderContentControlbarItem(
                              'auto_change_scene_off',
                            )}
                        {isChangeScene
                          ? LANGUAGES[languageProject]
                              .controlbarStopAutoPlayScene
                          : LANGUAGES[languageProject]
                              .controlbarStartAutoPlayScene}
                      </button>
                    </li>
                  )}
              </ul>
            </ControlbarPopoverWrapper>
          }
          overlayClassName="popoverControlbar"
        >
          <div className="actPopGroup">
            <button type="button" className="act">
              {onRenderContentControlbarItem('group_auto_play')}
            </button>
          </div>
        </Popover>
      )}
      {onCheckIsShowContentControlbarItem('view_mode') &&
        (onCheckIsShowContentControlbarItem('view_mode_little_planet') ||
          onCheckIsShowContentControlbarItem('view_mode_mirror') ||
          onCheckIsShowContentControlbarItem('view_mode_normal')) && (
          <div className="actPopGroup">
            <Popover
              placement="top"
              zIndex={99}
              content={
                <ControlbarPopoverWrapper>
                  <ul>
                    {onCheckIsShowContentControlbarItem('view_mode_normal') && (
                      <li>
                        <button type="button" onClick={() => onViewNormal()}>
                          {onRenderContentControlbarItem('view_mode_normal')}
                          Normal
                        </button>
                      </li>
                    )}
                    {onCheckIsShowContentControlbarItem('view_mode_mirror') && (
                      <li>
                        <button
                          type="button"
                          onClick={() => onViewMirrorBall()}
                        >
                          {onRenderContentControlbarItem('view_mode_mirror')}
                          Mirror Ball
                        </button>
                      </li>
                    )}
                    {onCheckIsShowContentControlbarItem(
                      'view_mode_little_planet',
                    ) && (
                      <li>
                        <button type="button" onClick={() => onViewPlannet()}>
                          {onRenderContentControlbarItem(
                            'view_mode_little_planet',
                          )}
                          Little Planel
                        </button>
                      </li>
                    )}
                  </ul>
                </ControlbarPopoverWrapper>
              }
              overlayClassName="popoverControlbar"
            >
              <button type="button" className="act">
                {onRenderContentControlbarItem('view_mode')}
              </button>
            </Popover>
          </div>
        )}

      {isShowIconMultipleStaging &&
        onCheckIsShowContentControlbarItem('multi_staging') && (
          <Tooltip
            title={
              isActiveModeMultipleStaging
                ? LANGUAGES[languageProject]
                    .controlbarTurnOffModeMultipleStaging
                : LANGUAGES[languageProject].controlbarTurnOnModeMultipleStaging
            }
            placement="top"
          >
            <button
              type="button"
              className="act hide-on-mobile"
              onClick={() => onToggleModeMultipleStaging()}
            >
              {onRenderContentControlbarItem('multi_staging')}
            </button>
          </Tooltip>
        )}

      {onCheckIsShowContentControlbarItem('vr') && (
        <Tooltip
          title={LANGUAGES[languageProject].controlbarVRMode}
          placement="top"
        >
          <button type="button" className="act" onClick={onVR}>
            {onRenderContentControlbarItem('vr')}
          </button>
        </Tooltip>
      )}

      {onCheckIsShowContentControlbarItem('fullscreen') && (
        <Tooltip
          title={LANGUAGES[languageProject].controlbarFullscreen}
          placement="top"
        >
          <button type="button" className="act" onClick={onFullscreen}>
            {onRenderContentControlbarItem('fullscreen')}
          </button>
        </Tooltip>
      )}

      {onCheckIsShowContentControlbarItem('gyro') && (
        <button
          type="button"
          className="act show-on-mobile"
          onClick={onToggleGyro}
        >
          {onRenderContentControlbarItem('gyro')}
        </button>
      )}
    </div>
  );
});

export default ControlbarAction;
